<template>
  <div class="withdraw-box">
    <NavBar />
    <span class="withdraw-box--title">提现</span>
    <div class="withdraw-card">
      <van-row>
        <van-col span="20" class="card-layout">
          <div class="card-content">
            <div class="card-name">{{ bankAccountInfo.accountName }}</div>
            <div class="card-bankname">{{ bankAccountInfo.bankName }}</div>
          </div>
        </van-col>
        <van-col span="4">
          <van-tag
            v-show="true || accountInfo.companyPaymentState == 'SUCCESS'"
            class="tag"
            @click="toChoose"
          >
            切换
            <van-icon name="exchange" class="tag-change" />
          </van-tag>
        </van-col>
      </van-row>
      <div class="card-num">
        <span>{{ bankAccountInfo.accountNo }}</span>
      </div>
    </div>
    <div class="field-layout">
      <van-field
        label="￥"
        class="withdraw-input"
        label-class="withdraw-label-class"
        :value="inputmoney"
        clickable
        readonly
        placeholder="请输入提现金额"
        :focused="showKeyboard"
        @touchstart.native.stop="showKeyboard = true"
      />
      <div class="all-money" @click="searchMoney">全部</div>
      <div class="withdarw-fee">
        <span>提现手续费</span><van-icon name="question" @click="toToast" />
      </div>
    </div>
    <van-goods-action>
      <van-button @click="toPassinfo" :class="[showKeyboard ? 'btn-up' : 'btn-down']" size="large"
        >下一步</van-button
      >
    </van-goods-action>
    <van-number-keyboard
      :show="showKeyboard"
      safe-area-inset-bottom
      v-model="inputmoney"
      class="withdraw-box--keyboard theme-keyboard-color"
      theme="custom"
      extra-key="."
      close-button-text="完成"
      :maxlength="9"
      @input="onInput(inputmoney)"
      @delete="onDelete"
      @close="showKeyboard = false"
    />
  </div>
</template>
<script>
import NavBar from '../navbar'
import { getYzm, getAccountInfo, txInfo, searchBank } from '../../api/index'
import { resFilter } from '@/utils/tool'
import { Field, Icon, Toast, Button, Col, Row, NumberKeyboard, GoodsAction, Tag } from 'vant'
import { format } from 'path'
export default {
  name: 'withdraw',
  components: {
    NavBar,
    [Field.name]: Field,
    [Icon.name]: Icon,
    [Toast.name]: Toast,
    [Button.name]: Button,
    [Row.name]: Row,
    [Col.name]: Col,
    [Tag.name]: Tag,
    [NumberKeyboard.name]: NumberKeyboard,
    [GoodsAction.name]: GoodsAction
  },
  data() {
    return {
      inputmoney: '', // 输入金额
      showKeyboard: true,
      code: this.$route.params.anCode || '', //商编
      accountInfo: {}, //账户信息
      bankAccountInfo: {
        bankName: '',
        accountName: '',
        accountNo: ''
      }, //银行账户信息
      bankNameInfo: [], //银行卡名称信息
      companyPaymentState: '', // 企业付款状态 NO: 未开通;SUCCESS: 已开通
      payacc: this.$route.query.payacc, //账户信息
      bindcard: this.$route.query.bindcard, //绑定银行卡号
      bank: this.$route.query.bank //银行名称
    }
  },
  destroyed() {
    Toast.clear()
  },
  created() {
    this.refer()
  },
  mounted() {
    // this.refer();
  },
  computed: {},
  methods: {
    async refer() {
      let res = resFilter(await getAccountInfo(this.code))
      // 调用提现接口获取卡片信息
      let cardInfo = resFilter(await txInfo(this.code))
      this.accountInfo = { ...cardInfo, ...res }
      // 根据bankID获取银行卡名称
      let bankInfo = await searchBank()
      this.bankNameInfo = bankInfo.data
      //不是从选择卡页面过来的
      if (!this.payacc) {
        if (this.accountInfo.privateNolegalpersonAccount) {
          this.bankNameInfo.forEach(e => {
            if (e.code == this.accountInfo.privateNolegalpersonAccount[0].bankId) {
              this.bankAccountInfo.bankName = e.name
            }
          })
        } else if (!this.accountInfo.privateNolegalpersonAccount) {
          this.bankNameInfo.forEach(e => {
            if (e.code == this.accountInfo.bankId) {
              this.bankAccountInfo.bankName = e.name
            }
          })
        }
        switch (this.accountInfo.companyPaymentState) {
          case 'SUCCESS':
            this.bankAccountInfo.accountName = this.accountInfo.privateNolegalpersonAccount[0].accountName
            this.bankAccountInfo.accountNo = this.accountInfo.privateNolegalpersonAccount[0].accountNo
            break
          case 'NO':
            this.bankAccountInfo.accountName = this.accountInfo.accountName
            this.bankAccountInfo.accountNo = this.accountInfo.accountNo
            break
        }
        return this.bankAccountInfo
      } else {
        this.bankAccountInfo.bankName = this.bank
        this.bankAccountInfo.accountName = this.payacc
        this.bankAccountInfo.accountNo = this.bindcard
      }
    },
    // 将“全部”可提现金额填充到输入框中
    searchMoney() {
      this.inputmoney = JSON.stringify(this.accountInfo.withdrawAmount)
    },
    // 提现手续费提示
    toToast() {
      Toast.setDefaultOptions({
        duration: 10000,
        overlay: true,
        closeOnClickOverlay: true,
        closeOnClick: true
      })
      Toast(
        '提现手续费：企业付款0.1%/笔，最低1元，封顶25元。(促销期执行优惠政策，企业付款按提现金额0.1%收取，最低1元/笔，最高1元/笔，促销期截止到2019年12月31日，如有变化另行通知)，企业付款手续费将从提现金额中扣除。'
      )
    },
    // 键盘输入
    onInput() {},
    // 键盘删除
    onDelete() {},
    // 切换
    toChoose() {
      this.$router.push({
        path: `/choosecard/${this.code}`
      })
    },
    // 获取验证码
    async toPhone() {
      let yzm = await getYzm(this.accountInfo.merchantContactPhone)
    },
    // 下一步跳转
    toPassinfo() {
      Toast.setDefaultOptions({ duration: 500 })
      Toast('为了演示方便，去掉了校验')
      // if (this.inputmoney == '') {
      //   Toast.fail('请输入提现金额')
      // } else if (this.inputmoney <= 1) {
      //   Toast.fail('企业付款金额必须大于1')
      // } else if (this.inputmoney > this.accountInfo.withdrawAmount) {
      //   Toast.fail('金额不能大于您的可提现金额')
      // } else {
      // 跳转的同时获取验证码
      this.toPhone()
      this.$router.push({
        path: `/passinfo/${this.code}`,
        query: {
          bindcard: this.bankAccountInfo.accountNo,
          bank: this.bankAccountInfo.bankName,
          payacc: this.bankAccountInfo.accountName,
          dollor: this.inputmoney
        }
      })
      // }
    }
  }
}
</script>
<style lang="less">
.withdraw-box {
  background: url(../../assets/images/ssg/green_withdraw.png) no-repeat;
  background-size: 100%;
  width: 100%;
  font-size: @base-size * 1.4;
  box-sizing: border-box;
  padding-bottom: @base-size*2;
  &--title {
    font-size: @base-size*2.8;
    color: @ft-white;
    margin: 20px 0px 20px 20px;
  }

  &--keyboard {
    color: @ft-black;
    .van-key--blue,
    .van-key--blue.van-key--active {
      background-color: @btn-color-ssg;
      &:active {
        opacity: 0.9;
      }
      &::after {
        border-color: @btn-color-ssg;
      }
    }
  }

  .withdraw-card {
    width: 90%;
    height: 100%;
    margin: auto;
    border-radius: @base-size;
    background: url(../../assets/images/default_card.png) no-repeat center;
    background-size: 100%;
    transform: translateY(@base-size * 2.3);
    padding: @base-size*2 0;
    .card-layout {
      display: flex;
      justify-content: flex-start;
      flex-flow: row nowrap;
      align-items: center;
      .change-img {
        height: @base-size*3.2;
        width: @base-size*7;
        margin: auto -0.7% auto auto;
      }
      .card-content {
        padding-left: @base-size*6.5;
        color: @ft-color-ssg;
        .card-name {
          font-size: @base-size * 1.4;
          line-height: 1em;
          padding-top: @base-size*4;
        }
        .card-bankname {
          font-size: @base-size * 1.4;
          color: @ft-color-ssg;
          line-height: 1em;
        }
      }
    }
    .card-num {
      display: block;
      font-size: @base-size * 2.4;
      color: @ft-color-ssg;
      margin: @base-size*2.5 0 @base-size*2.5 @base-size*3.5;
    }
    .tag {
      font-size: @base-size * 1.2;
      width: @base-size * 5.4;
      height: @base-size * 2.2;
      background-color: @tag-ssg;
      justify-content: flex-end;
      border-radius: @base-size*1.5 0 0 @base-size*1.5;
      box-shadow: 0 @base-size*.35 @base-size*.35 @base-size*.035 @tag-shadow-color-ssg;
      margin-top: @base-size*2;
      margin-left: -@base-size;
      .tag-change {
        width: @base-size * 1.4;
        height: @base-size * 1.4;
        border-radius: 50%;
        background-color: @ft-white;
        color: @tag-ssg;
        margin-left: @base-size * 0.8;
        &::before {
          transform: translate(@base-size * 0.1, @base-size * 0.15);
        }
      }
    }
  }
  .field-layout {
    margin-top: @base-size*4;
    .withdraw-input {
      font-size: @base-size*2.2;
      border-bottom: @base-size*.1 solid @btn-color-ssg;
      width: 80%;
      margin-left: @base-size*2;
      background-color: rgba(0, 0, 0, 0);
    }
    .withdraw-label-class {
      font-size: @base-size*2.0;
      width: auto;
      padding-right: @base-size;
    }
    .all-money {
      z-index: 999;
      color: @btn-color-ssg;
      float: right;
      margin: -@base-size*3.5 @base-size*2 0 0;
      font-size: @base-size*1.8;
    }
  }
  .btn-down {
    width: 90%;
    display: block;
    margin: @base-size*2 auto;
    font-size: @base-size*1.8;
    color: @ft-color-ssg;
    background-color: @btn-color-ssg;
    border-radius: @base-size*5;
    font-weight: bold;
  }
  .btn-up {
    width: 90%;
    display: block;
    font-size: @base-size*1.8;
    color: @ft-color-ssg;
    margin: 0 auto;
    background-color: @btn-color-ssg;
    border-radius: @base-size*5;
    transform: translateY(-230px);
    font-weight: bold;
  }
  .withdarw-fee {
    float: right;
    margin: @base-size;
    color: @info-number-color-ssg;
    font-size: @base-size;
  }
}
</style>
